.login-container {
  max-width: 400px;
  max-height: 300px;
  margin: 80px 0 0 40px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-family: Arial, sans-serif;

  display: flex;
  flex-direction: column; 
}

.field {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}


.login-form {
  display: flex;
  flex-direction: column;
}

.login-form label {
  margin-top: 12px;
  font-weight: bold;
}

.login-form input {
  margin-top: 5px;
  padding: 8px;
  font-size: 14px;
}

.login-form button {
  margin-top: 20px;
  padding: 10px;
  background-color: #2563eb;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.login-form button:hover {
  background-color: #1e40af;
}

.welcome {
  text-align: center;
  color: green;
  font-weight: bold;
}
